<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">修改地址</span>
      </div>
      <div class="content-wrapper">
        <ul class="content">
          <li class="item">
            <label>收货人</label>
            <div class="input-wrapper">
              <input type="text" v-model="contacts" />
            </div>
          </li>
          <li class="item">
            <label>联系电话</label>
            <div class="input-wrapper">
              <input type="text" v-model="contactNumber" />
            </div>
          </li>
          <li class="item">
            <label>收货地址</label>
            <div class="input-wrapper">
              <input type="text" v-model="address" />
            </div>
          </li>
        </ul>
        <div class="btn-wrapper">
          <span class="btn" @click="submit">保存</span>
        </div>
      </div>
      <alert ref="alert" :message="alertMsg"></alert>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Alert from 'base/alert/alert'
  import {ERR_CODE} from 'api/config'
  import {isMobile,isZuoJi} from 'common/js/util'
  import {mapGetters,mapState,mapActions} from 'vuex'
  import {getAddress,addAddresss,updateAddress} from 'api/purchase'

  export default {
    data () {
      return {
        contacts: '',
        contactNumber: '',
        address: '',
        alertMsg: ''
      }
    },
    created () {
      this._getAddress()
    },
    computed: {
      ...mapState([
        'addressInfo'
      ]),
    },
    methods: {
      back() {
        this.$router.back()
      },
      _getAddress() {
        getAddress().then((res) => {
          if(res.errCode === ERR_CODE){
            if(res.data && res.data.length){
              let address = res.data[0]
              this.id = address.id
              this.contacts = address.contacts
              this.contactNumber = address.contactNumber
              this.address = address.address
            }
          }
        })
      },
      _check(){
        let contacts = this.contacts
        let contactNumber = this.contactNumber
        let address = this.address
        if(!contacts){
          this.alertMsg = '请输入收货人'
          this.showAlert()
          return false
        }
        if(!contactNumber){
          this.alertMsg = '请输入联系电话'
          this.showAlert()
          return false
        } else if(!isMobile(contactNumber) && !isZuoJi(contactNumber)){
          this.alertMsg = '请输入正确的联系电话'
          this.showAlert()
          return false
        }
        if(!address){
          this.alertMsg = '请输入收货地址'
          this.showAlert()
          return false
        }
        return true
      },
      submit() {
        if(!this._check()){
          return
        }
        let contacts = this.contacts
        let contactNumber = this.contactNumber
        let address = this.address
        if(this.id){
          updateAddress({
            id:this.id,
            contacts:contacts,
            contactNumber:contactNumber,
            address:address
          }).then((res) => {
            if(res.errCode === ERR_CODE){
              this.setAddressInfo({
                ...this.addressInfo,
                id:this.id,
                contacts,
                contactNumber,
                address
              })
              this.$router.back()
            }
          })
        } else {
          addAddresss({
            contacts:contacts,
            contactNumber:contactNumber,
            address:address
          }).then((res) => {
            if(res.errCode === ERR_CODE){
              this.setAddressInfo({
                ...this.addressInfo,
                id: res.data.id,
                contacts:contacts,
                contactNumber:contactNumber,
                address:address
              })
              this.$router.back()
            }
          })
        }
      },
      showAlert() {
        this.$refs.alert.show()
      },
      ...mapActions([
        'setAddressInfo'
      ])
    },
    components: {
      Alert
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        display flex
        flex-direction column
        height 192px
        width 100%
        margin-top 10px
        background #fff
        .item
          display flex
          flex 1
          align-items center
          margin-left 15px
          margin-right 15px
          font-size $font-size-medium
          color #333
          label
            display flex
            width 90px
          .input-wrapper
            display flex
            flex 1
            align-items center
            height 100%
            input
              display block
              width 100%
              height 100%
              box-sizing border-box
              padding-left 15px
              padding-right 15px
              outline none
      .btn-wrapper
        margin-top 30px
        display flex
        justify-content center
        .btn
          width 280px
          height 40px
          display flex
          align-items center
          justify-content center
          color #fff
          background #EB4747
          border-radius 5px
          font-size $font-size-large

</style>



